@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:wght@700&display=swap');



:root {
  --primary-cinza-leve: #EFEFEF;
  --primary-marrom: #BFB5A2;
  --primary-marron-claro: #D6CEBF;
  --primary-branco: #FFFFFF;
  --primary-preto: #303030;
  --primary-laranja: #D6613E;
  --primary-cinza-marrom-claro: #646464;
  --primary-cinza-marrom-escuro: #000000;
  --primary-cinza: #C8C8C8;
  --primary-cinza-escuro: #858585;
 
}

body {
  overflow-x: hidden !important;
}


/*************************** NAVBAR ********************************/

.navbar {
  transition: all 0.3s;
  border-bottom: 21px solid var(--primary-marron-claro);
}

@media(max-width: 768px) {
  .navbar {
    border-bottom-width: 10px;  
    width: 390px; 
    position: fixed;
  }
}

@media(max-width: 768px) {
  .sticky-top{
    display: none;  
    position: initial !important; 
  }
}


.navbar-brand img{
  float: none;
  position: absolute;
  left: 50%;
  margin-left: -7.8% !important;  /* 50% of your logo width */
  margin-top: -3.5rem;
  display: block;
  background-color: var(--primary-branco);
  border: solid var(--primary-branco);
  border-top-width: 25px ;
  border-bottom-width: 20px ;
  border-right-width: 13px ;
  border-left-width: 13px ;
  border-radius: 0px 0px 4px 4px;
  transition: all 0.3s;
}

@media(max-width: 768px) {
  .navbar-brand img{
    display: none;
  }
}

.navbar-header {
  margin-top: -1rem;
}

.color-nav {
  background-color: var(--primary-marrom);
} 

.navbar-toggler {
  color: var(--primary-preto);
  background-color: var(--primary-marrom) !important;
}

@media(max-width: 768px) {
  .navbar-toggler {
    margin-bottom: 0.2rem;
  }
}

@media(max-width: 768px) {
  .collapse {
    margin-left: -1.8rem;
    transition: all 0.1s;
  }
}

.nav-link {
  color: var(--primary-cinza-marrom-escuro);
  margin-left: 2rem;
}

.navbar-nav a  {
  font-family: 'Noto Sans', sans-serif ;
  color: black;
  font-size: 14px;
  text-transform: uppercase;
  margin-right: 0.2rem;
  margin-left: 1.2rem;
  transition: all 0.1s;
}

.navbar-icon {
  margin-right: 8rem;
  margin-left: -2rem;
  margin-top: 1rem;
}

.navbar-icon a{
  margin-right: -1rem;
  margin-left: 2rem;
  
   
}

.navbar-icon-car {
  margin-left: -5rem;
  position: relative;
}

.quantidade-carrinho {
  background-color: var(--primary-laranja);
  color: var(--primary-branco);
  font-family: 'Noto Sans', sans-serif ;
  border: 3px solid var(--primary-laranja);
  border-radius: 6px;
  font-size: 14px;
}

.carrinho-link:link {
  text-decoration: none;
}

@media(max-width: 768px) {
  .carrinho-link {
    left: 1rem;
  }
}


@media(max-width: 768px) {
  .navbar-icon {
    display: none;
  }
}

.navbarS {
  border-bottom: 12px solid var(--primary-marron-claro);
  height: 60px;
  opacity: 95%;
  transition: all 0.3s;
}

@media(max-width: 768px) {
  .navbarS {
    height: 110px;
    position: fixed;
  }
}

.navbarS .navbar-icon { 
  display: none;
  transition: all 0.3s;
}

.navbarS .navbar-icon-car { 
  display: none;
  transition: all 0.3s;
}

.navbarS .navbar-brand img {
  max-height: 110px;
  max-width: 110px;
  margin-top: -2.5rem;  
  transition: all 0.3s;
  border-bottom-width: 9px;
}

@media(max-width: 768px) {
  .navbarS .navbar-brand img {
  max-height: 105px;
  max-width: 105px;
  margin-top: -2.5rem;  
  transition: all 0.3s;
  border-bottom-width: 9px;
  }
}

.navbarS .quantidade-carrinho {
  display: none;
}

.navbarS .navbar-nav {
  margin-top: -1.5rem;
  transition: all 0.1s;
}

.color-nav {
  background-color: var(--primary-marrom);
} 

